.Home {
    width: 100%;
    min-height: calc(100vh - 58px);
}
.Spinner {
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;    
}

.lds-grid {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
  }
  .lds-grid div {
    position: absolute;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #8756ca;
    -webkit-animation: lds-grid 1.2s linear infinite;
            animation: lds-grid 1.2s linear infinite;
  }
  .lds-grid div:nth-child(1) {
    top: 6px;
    left: 6px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
  }
  .lds-grid div:nth-child(2) {
    top: 6px;
    left: 26px;
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
  }
  .lds-grid div:nth-child(3) {
    top: 6px;
    left: 45px;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
  }
  .lds-grid div:nth-child(4) {
    top: 26px;
    left: 6px;
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
  }
  .lds-grid div:nth-child(5) {
    top: 26px;
    left: 26px;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
  }
  .lds-grid div:nth-child(6) {
    top: 26px;
    left: 45px;
    -webkit-animation-delay: -1.2s;
            animation-delay: -1.2s;
  }
  .lds-grid div:nth-child(7) {
    top: 45px;
    left: 6px;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
  }
  .lds-grid div:nth-child(8) {
    top: 45px;
    left: 26px;
    -webkit-animation-delay: -1.2s;
            animation-delay: -1.2s;
  }
  .lds-grid div:nth-child(9) {
    top: 45px;
    left: 45px;
    -webkit-animation-delay: -1.6s;
            animation-delay: -1.6s;
  }
  @-webkit-keyframes lds-grid {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0.5;
    }
  }
  @keyframes lds-grid {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0.5;
    }
  }

.channels {
    display: grid;
    grid-gap: 15px;
    padding: 15px;
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
}
.Channel {
    display: inline-block;
    width: 100%;
    vertical-align: top;
}

.Channel:hover .Channel-cover {
    box-shadow: 0 0 10px rgba(0,0,0,.3);
}

.Channel-image {
    object-fit: cover;
    cursor: pointer;
    border-radius: 3px;
    box-shadow: 0px 2px 6px rgba(0,0,0,0.15);
    width: 100%;
}
  
.Channel-title {
    cursor: default;
    margin: 5px 0 0 0;
    color: #44546b;
    white-space: normal;
    font-size: 0.9em;
    font-weight: 600;
    text-align: center;
}
.ChannelPage-content {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.ChannelPage-banner img {
    width: 100%;
}

.ChannelPage h2 {
    text-align: center;
    font-size: 2em;
}
.Podcast {
    display: -webkit-flex;
    display: flex;
    text-decoration: none;
    color: #333;
    padding: 15px;
    border-bottom: 1px solid rgba(0,0,0,0.2);
    cursor: pointer;
}

.Podcast:hover {
    color: #000;
}

.Podcast h4 {
    margin: 0;
    font-size: 1em;
}

.Podcast p {
    color: #666;
    margin-top: 0.5em;
    margin-bottom: 0;
    font-size: 0.8em;
}

.Podcast-details {
    margin-left: 1em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}




